// ConfirmationModal.jsx
import React, { useState } from "react";
import CustomModal from "../../components/Modal/customModal";
import { ExcelProcessingService } from "../../pages/mass-upload/ExcelProcessingService";
import ProgressBar from "react-bootstrap/ProgressBar";

const ConfirmationModal = ({ show, onHide, onConfirm, file }) => {
  const [progress, setProgress] = useState(0);
  const [isProcessing, setIsProcessing] = useState(false);

  const handleConfirm = async () => {
    if (!file) return;

    setIsProcessing(true);
    const excelService = new ExcelProcessingService();

    try {
      const batches = await excelService.processExcelFile(file);

      // Process each batch
      let processedBatches = 0;
      for (const batch of batches) {
        // Here you would send the batch to your API
        console.log("Processing batch:", batch);

        // Update progress
        processedBatches++;
        const currentProgress = (processedBatches / batches.length) * 100;
        setProgress(currentProgress);

        // Simulate API call delay
        await new Promise((resolve) => setTimeout(resolve, 500));
      }

      onConfirm();
      setIsProcessing(false);
      onHide();
    } catch (error) {
      console.error("Batch processing failed:", error);
      setIsProcessing(false);
    }
  };

  return (
    <CustomModal
      show={show}
      onHide={onHide}
      title="Confirmation"
      footerButtons={[
        {
          label: "Cancel",
          variant: "danger",
          onClick: onHide,
          disabled: isProcessing,
        },
        {
          label: isProcessing ? "Processing..." : "Confirm",
          variant: "success",
          onClick: handleConfirm,
          disabled: isProcessing,
        },
      ]}
    >
      <p>Are you sure you want to submit?</p>
      {isProcessing && (
        <div className="mt-3">
          <ProgressBar
            now={progress}
            label={`${Math.round(progress)}%`}
            animated
          />
          <p className="text-center mt-2">
            Processing data in batches... ({Math.round(progress)}%)
          </p>
        </div>
      )}
    </CustomModal>
  );
};

export default ConfirmationModal;
