import * as XLSX from 'xlsx';

const BATCH_SIZE = 1000; // Configurable batch size

export class ExcelProcessingService {
  async processExcelFile(file) {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      
      reader.onload = async (e) => {
        try {
          const workbook = XLSX.read(e.target.result, { type: 'binary' });
          const worksheet = workbook.Sheets[workbook.SheetNames[0]];
          const data = XLSX.utils.sheet_to_json(worksheet);
          
          // Process data in batches
          const batches = this.createBatches(data);
          resolve(batches);
        } catch (error) {
          reject(error);
        }
      };

      reader.onerror = () => reject(new Error('File reading failed'));
      reader.readAsBinaryString(file);
    });
  }

  createBatches(data) {
    const batches = [];
    for (let i = 0; i < data.length; i += BATCH_SIZE) {
      const batch = data.slice(i, i + BATCH_SIZE);
      const transformedBatch = this.transformBatchToPayload(batch);
      batches.push(transformedBatch);
    }
    return batches;
  }

  transformBatchToPayload(batch) {
    return {
      items: batch.map(row => ({
        type: "VPMReference",
        attributes: {
          title: row.Title || '',
          description: row.Description || '',
          "dseno:EnterpriseAttributes": {
            "XP_VPMReference_Ext.EMR_ERP_PrimaryUOM": row["Unit Of Measure"] || ''
          },
          "dseng:EnterpriseReference": {
            partNumber: row["EIN Number"] || ''
          }
        },
        classificationType: row["Type"] || "Standard",
        collabspace: row["Collaborative Space"] || ''
      })),
      background: false,
      emailNotification: false,
      securityContext: `VPLMProjectLeader.0000000001.${batch[0]["Collaborative Space"] || ''}`,
      userId: "emrserviceuser"
    };
  }
}